* {
  font-family: Roboto, sans-serif;
}

a, a:hover {
  text-decoration: none;
}

img {
  vertical-align: middle;
}

a, h1, h2, h3, h4, h5, h6, li, p, ul {
  word-break: break-word;
  margin: 0;
  padding: 0;
}

:focus {
  outline: -webkit-focus-ring-color auto 0;
  outline-color: -webkit-focus-ring-color;
  outline-style: 0;
  outline-width: 0;
}

.btn.focus, .btn:focus {
  box-shadow: none !important;
}

.form-control:focus {
  border-color: var(--brand-color) !important;
  box-shadow: none !important;
}

::-webkit-scrollbar {
  width: .4em;
  height: .4em;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 3px #0000004d;
}

::-webkit-scrollbar-thumb {
  background-color: var(--brand-color);
  outline: 1px solid var(--brand-color);
}

input::-webkit-inner-spin-button, input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="password"]::-ms-clear, input[type="password"]::-ms-reveal {
  display: none;
}

:root {
  --text-color: #29303b;
  --text-light-color: #2c2c2c;
  --subtitle-color: #848484;
  --brand-color: #3c3892;
  --brand-color2: #3c3892;
  --brand-color3: #7471b1;
  --button-color: var(--brand-color);
  --review-color: #0fbfa1;
  --review-gold-color: #ffc107;
  --border-color: #c4bbbb;
}

.section-title {
  color: var(--button-color);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  background: linear-gradient(to right, #4d4fe0, #3d005c);
}

h1.title {
  color: #fff;
  text-align: center;
  margin-bottom: 40px;
  font-size: 40px;
  font-weight: 400;
}

.form {
  height: 40px;
  margin-bottom: 10px;
  display: flex;
}

input, select {
  text-align: center;
  border: 1px solid #ccc;
}

#amount {
  max-width: 30%;
  padding: 0 0 0 15px;
}

.form select {
  min-width: 500px;
  flex: 1;
  padding-left: 8px;
}

#symbol {
  max-width: 25%;
  text-align: center;
}

footer {
  color: #3dff11;
  padding: 15px 0;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

footer a, footer a:hover {
  color: #3dff11;
  text-decoration: none;
}

